import WordCloudComponent from "../Wordcloud/wordcloud";

// Home component
const Home = () => {
  return (
    // Section for the home page with id "home" and flex layout
    <section id="home" className="d-flex justify-content-center">
      <div className="container-fluid">
        {/* Word Cloud section */}
        <div id="word-cloud" className="d-flex justify-content-center p-3">
          <WordCloudComponent />
        </div>
        {/* Description section */}
        <div className="d-flex justify-content-center p-3">
          <p>
            The Behavioral Science Lab at
            <a
              href="https://bloomington.iu.edu/index.html"
              target="_blank"
              rel="noreferrer"
            >
              {" "}
              Indiana University{" "}
            </a>
            in the
            <a
              href="https://psych.indiana.edu/index.html"
              target="_blank"
              rel="noreferrer"
            >
              {" "}
              Department of Psychological and Brain Sciences{" "}
            </a>
            works at the interface of behavioral economics and cognitive science
            with a focus on how people make judgments and decisions and how they
            can make better judgments and decisions. Our team, led by
            <a
              href="https://www.timpleskac.com/"
              target="_blank"
              rel="noreferrer"
            >
              {" "}
              Dr. Tim Pleskac
            </a>
            , is a collaborative group of undergraduate, graduate, post doc,
            faculty, and community researchers.
          </p>
        </div>
      </div>
    </section>
  );
};

// Exporting the Home component
export default Home;
