import React, { useState } from "react";
import "./navbar.css";
import { NavLink } from "react-router-dom";
import penrose from "../../assets/penrose.svg";

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <nav className="navbar mb-3">
      <div className="navbar-title">
        <img src={penrose} alt="Logo" className="logo" />
        <NavLink to="/home" className="custom-link">
          <h2>
            <b>IU BEHAVIORAL SCIENCE LABORATORY</b>
          </h2>
        </NavLink>
      </div>

      {/* Hamburger menu for mobile */}
      <div className="hamburger-menu" onClick={toggleMenu}>
         Menu
      </div>

      <div className={`navbar-items ${isMenuOpen ? "show" : ""}`}>
        <NavLink to="/home" className="custom-link desktopMenuListItem">
          Home
        </NavLink>
        <NavLink to="/research" className="custom-link desktopMenuListItem">
          Research
        </NavLink>
        <NavLink to="/publications" className="custom-link desktopMenuListItem">
          Publications
        </NavLink>
        <NavLink to="/team" className="custom-link desktopMenuListItem">
          Team
        </NavLink>
        <NavLink to="/facilities" className="custom-link desktopMenuListItem">
          Facilities
        </NavLink>
        <NavLink to="/joinus" className="custom-link desktopMenuListItem">
          Join Us
        </NavLink>
      </div>
    </nav>
  );
};

export default Navbar;
