import BibTexParser from "bibtex-parser-js";

const getWordFrequencies = async (filePath, limit = 50) => {
  try {
    // Fetch the BibTeX file
    const response = await fetch(filePath);
    if (!response.ok) {
      console.error(`Failed to fetch BibTeX file: ${response.statusText}`);
      return [];
    }
    const data = await response.text();

    let parsedData;
    try {
      parsedData = BibTexParser.toJSON(data);
    } catch (parseError) {
      console.error("Error parsing BibTeX data:", parseError);
      return [];
    }
    console.log("Parsed BibTeX Data:", parsedData);

    if (!parsedData || parsedData.length === 0) {
      console.error("No valid BibTeX entries found.");
      return [];
    }

    const abstracts = parsedData
      .map((entry) => {
        const abstractKey = Object.keys(entry.entryTags).find(
          (key) => key.toLowerCase() === "abstract"
        );
        const abstract = abstractKey ? entry.entryTags[abstractKey] : "";
        if (!abstract.trim()) {
          console.warn("No abstract found for entry:", entry);
        }
        return abstract.trim();
      })
      .filter(Boolean);

    if (abstracts.length === 0) {
      return [];
    }

    const combinedText = abstracts.join(" ").toLowerCase();

    const words = combinedText.match(/\b[a-z]{4,}\b/g);
    console.log("Matched Words:", words);

    if (!words) {
      console.error("No words matched the regex.");
      return [];
    }

    const frequencies = words.reduce((freq, word) => {
      freq[word] = (freq[word] || 0) + 1;
      return freq;
    }, {});
    console.log("Word Frequencies:", frequencies);
    const excludedWords = [
      "that",
      "this",
      "than",
      "when",
      "what",
      "both",
      "also",
      "over",
      "only",
      "with",
      "have",
      "from",
      "were",
      "they",
      "show",
      "tasks",
      "used",
      "each",
      "such",
      "which",
      "these",
      "their",
      "three",
      "other",
      "about",
      "others",
      "authors",
      "decisions",
      "processes",
      "models",
      "judgments",
      "probabilities",
      "choices",
      "make",
      "making",
    ];

    const sortedFrequencies = Object.entries(frequencies)
      .sort(([, a], [, b]) => b - a)
      .filter(([word]) => !excludedWords.includes(word))
      .slice(0, limit);
    console.log("Top Word Frequencies (Filtered):", sortedFrequencies);
    return sortedFrequencies;
  } catch (error) {
    console.error("Error fetching or processing BibTeX:", error);
    return [];
  }
};

export default getWordFrequencies;
