import React, { useEffect, useState } from "react";
import BibTexParser from "bibtex-parser-js";
import "./publications.css";

const Publications = () => {
  const [publications, setPublications] = useState([]);
  const [sortOrder, setSortOrder] = useState("year-reverse");
  const [selectedTag, setSelectedTag] = useState(""); // State to track the selected tag
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    fetch("/IUBehSciLib.bib")
      .then((response) => response.text())
      .then((data) => {
        const parsedData = BibTexParser.toJSON(data);
        setPublications(parsedData);
      });
  }, []);

  const sortedPublications = [...publications]
    .filter((publication) => {
      if (!selectedTag) return true; // No tag selected, show all publications
      if (selectedTag === "All") return true;
      const topic = (publication.entryTags.TOPICS || "").toLowerCase();
      return topic.includes(selectedTag.toLowerCase());
    })

    //search
    .filter((pub) => {
      const query = searchQuery.trim().toLowerCase();
      if (!query) return true;

      const authorsStr = (
        pub.entryTags.AUTHOR ||
        pub.entryTags.author ||
        ""
      ).toLowerCase();

      const abstractStr = (
        pub.entryTags.ABSTRACT ||
        pub.entryTags.abstract ||
        ""
      ).toLowerCase();

      const titleStr = (
        pub.entryTags.TITLE ||
        pub.entryTags.title ||
        ""
      ).toLowerCase();

      return (
        authorsStr.includes(query) ||
        abstractStr.includes(query) ||
        titleStr.includes(query)
      );
    })

    //search

    .sort((a, b) => {
      const yearA = parseInt(a.entryTags.YEAR || a.entryTags.year, 10);
      const yearB = parseInt(b.entryTags.YEAR || b.entryTags.year, 10);

      if (sortOrder === "year") {
        return yearA - yearB;
      } else if (sortOrder === "year-reverse") {
        return yearB - yearA;
      }
      return 0;
    });

  const handleSortChange = (event) => {
    setSortOrder(event.target.value);
  };

  const handleTagClick = (tag) => {
    setSelectedTag(tag === selectedTag ? "" : tag);
  };

  return (
    <section id="publications" className="d-flex justify-content-center">
      <div className="pubs-grid-container">
        <div className="pubs-grid-header">
          <h2>
            <b>PUBLICATIONS</b>
          </h2>
          {/*Search Input*/}
          <input
            type="text"
            id="pubSearch"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            placeholder="Search by author/title/abstract"
            className="pub-search-input"
          />
          {/* */}
          <div className="filters-container">
            <div className="sort-container">
              <label htmlFor="sort-filter" className="sort-label">
                Sort by:
              </label>
              <select
                id="sort-filter"
                value={sortOrder}
                onChange={handleSortChange}
                className="sort-filter"
              >
                <option value="year">Year (Ascending)</option>
                <option value="year-reverse">Year (Descending)</option>
              </select>
            </div>
          </div>
        </div>
        <hr />
        {/* tags code */}
        <div className="tags-container">
          <span className="tags-label">Tags:</span>
          <br></br>
          <br></br>
          <ul className="tags-list">
            {[
              "All",
              "deliberation",
              "choice environments",
              "translational modeling",
              "uncertainty",
              "cognition",
            ].map((tag, index) => (
              <li
                key={index}
                className={`tag-item ${
                  selectedTag === tag ? "selected-tag" : ""
                }`}
                onClick={() => handleTagClick(tag)}
              >
                {tag}
              </li>
            ))}
          </ul>
        </div>
        <hr />
        {/* tags code */}
        <div className="pubs-list">
          {sortedPublications.length > 0 ? (
            <ul>
              {sortedPublications.map((publication, index) => (
                // className="publication-card" remove this for previous functionality
                <li key={index} className="publication-card">
                  <strong>
                    {publication.entryTags.TITLE || publication.entryTags.title}
                  </strong>
                  <br />
                  Author(s):{" "}
                  {publication.entryTags.AUTHOR || publication.entryTags.author}
                  <br />
                  Journal:{" "}
                  {publication.entryTags.JOURNAL ||
                    publication.entryTags.journal}
                  <br />
                  Year:{" "}
                  {publication.entryTags.YEAR || publication.entryTags.year}
                  <br />
                  {publication.entryTags.DOI || publication.entryTags.doi ? (
                    <span>
                      doi:{" "}
                      <a
                        href={`https://doi.org/${
                          publication.entryTags.DOI || publication.entryTags.doi
                        }`}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        {publication.entryTags.DOI || publication.entryTags.doi}
                      </a>
                    </span>
                  ) : null}
                </li>
              ))}
            </ul>
          ) : (
            <p>No publications available.</p>
          )}
        </div>
      </div>
    </section>
  );
};

export default Publications;
