import React from "react";
import { Link } from "react-router-dom";
import "./footer.css";

// Define the Footer component
const Footer = () => {
  return (
    <footer className="footer text-center lg:text-left">
      {/* Container for footer content with padding */}
      <div className="p-4">
        <div className="footer-bar flex justify-between items-center text-neutral-700 dark:text-neutral-200">
          {/* Information section on the left */}
          <div>
            <Link to="/privacy-policy" className="">
              Privacy Policy
            </Link>
            <br></br>
            IU Behavioral Science Laboratory maintained by{" "}
            <a
              className="text-neutral-800 dark:text-neutral-400"
              href="https://github.iu.edu/behave"
            >
              behave
            </a>
            <p>
              Published with{" "}
              <a
                className="text-neutral-800 dark:text-neutral-400"
                href="https://kb.iu.edu/d/axnv"
              >
                Sitehost
              </a>
            </p>
          </div>
        </div>
      </div>
    </footer>
  );
};

// Exporting the Footer component
export default Footer;

