import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';

// Creating a root for ReactDOM
const root = ReactDOM.createRoot (document.getElementById('root'));

// Rendering the App component within the root
root.render(
    // Wrapping the App component with React.StrictMode for additional development features
    <React.StrictMode>
        <App />
    </React.StrictMode>
);