import React from "react";
import "../Facilities/facilities.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
// import { Navigation, Pagination, Autoplay } from "swiper/modules";
import { Navigation, Pagination } from "swiper/modules";
import Slide1 from "../../assets/Lab_Space.jpg";
import Slide2 from "../../assets/Lab_Space1.jpg";
import Slide3 from "../../assets/Facilities.jpg";
import Slide4 from "../../assets/Facilities_2.jpg";
import Slide5 from "../../assets/Outside_Space.jpg";
import Slide6 from "../../assets/Xiaohong_office.jpg";
import Slide7 from "../../assets/James_Office.jpg";
import Slide8 from "../../assets/Lab_Working.jpg";
import Slide9 from "../../assets/JAdamChess3.jpg";

const Facilities = () => {
  return (
    <div id="facilities">
      {/* Facilities Section */}
      <div className="facilities">
        <h2>Lab Facilities</h2>

        <Swiper
          spaceBetween={20}
          slidesPerView={1}
          loop={true}
          autoplay={{ delay: 3000, disableOnInteraction: true }}
          pagination={{ clickable: true }}
          navigation={true}
          // modules={[Navigation, Pagination, Autoplay]}
          modules={[Navigation, Pagination]}
          className="facilities-slider"
        >
          <SwiperSlide>
            <img src={Slide1} alt="Facility 1" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide2} alt="Facility 2" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide3} alt="Facility 3" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide4} alt="Facility 4" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide5} alt="Facility 5" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide6} alt="Facility 6" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide7} alt="Facility 7" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide8} alt="Facility 8" />
          </SwiperSlide>
          <SwiperSlide>
            <img src={Slide9} alt="Facility 9" />
          </SwiperSlide>
        </Swiper>
        <br></br>
        <br></br>
      </div>
      {/* Lab Location Section */}
      <div className="grid-container">
        <div className="grid-item">
          <br></br>
          <h2>
            <b>LAB LOCATION</b>
          </h2>
          <hr />
          <p>
            <b>Building: </b>
            <a
              href="https://igws.indiana.edu/about/facilities"
              target="_blank"
              rel="noopener noreferrer"
            >
              Geology Building
            </a>
          </p>
          <p>
            <b>Address:</b> 1001 E. 10th Street, Bloomington, IN, 47405
          </p>
          <p>
            <b>Rooms: </b>6th Floor (GeoLab)
          </p>
        </div>

        {/* Container for the lab location map */}
        <div className="map-item">
          <iframe
            title="Lab Location Map"
            //src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3093.1143034255233!2d-86.52267662400462!3d39.1721221304475!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x886c66c7635927bd%3A0x78888eb4a1b46d01!2sIUB%20Psychological%20%26%20Brain%20Sciences!5e0!3m2!1sen!2sus!4v1699557336653!5m2!1sen!2sus"
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3093.1089635921003!2d-86.5239397245807!3d39.17224353044118!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x886c66c71083be5b%3A0x272c9979bf811b53!2sGeology%20Building%2C%20611%20N%20Walnut%20Grove%20St%2C%20Bloomington%2C%20IN%2047405!5e0!3m2!1sen!2sus!4v1741210206862!5m2!1sen!2sus"
            width="600"
            height="450"
            style={{ border: "0" }}
            allowFullScreen=""
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
          ></iframe>
        </div>
        <br></br>
      </div>
    </div>
  );
};

export default Facilities;
