import React, { useEffect, useState } from "react";
import WordCloud from "wordcloud";
import getWordFrequencies from "../Wordcloud/getWordFrequencies"; // Adjust the path as needed

const WordCloudComponent = () => {
  const [wordFrequencies, setWordFrequencies] = useState([]);

  useEffect(() => {
    // Fetch and set word frequencies dynamically
    const fetchData = async () => {
      const frequencies = await getWordFrequencies("/IUBehSciLib.bib", 50);
      setWordFrequencies(frequencies);
    };

    fetchData();
  }, []);

  useEffect(() => {
    if (wordFrequencies && wordFrequencies.length > 0) {
      const isMobile = window.innerWidth < 768;

      WordCloud(document.getElementById("wordcloud"), {
        list: wordFrequencies,
        gridSize: isMobile ? 10 : 16,
        weightFactor: (size) => Math.max(Math.pow(size, 0.89), 10),
        fontFamily: "Times, serif",
        color: "random-dark",
        backgroundColor: "#fff",
        rotateRatio: isMobile ? 0.1 : 0.2,
        ellipticity: isMobile ? 1 : 0.6,
      });

      const handleResize = () => {
        document.getElementById("wordcloud").innerHTML = "";
        WordCloud(document.getElementById("wordcloud"), {
          list: wordFrequencies,
          gridSize: window.innerWidth < 768 ? 10 : 16,
          weightFactor: (size) => Math.max(Math.pow(size, 0.875), 10),
          fontFamily: "Times, serif",
          color: "random-dark",
          backgroundColor: "#fff",
          rotateRatio: window.innerWidth < 768 ? 0.1 : 0.2,
          ellipticity: window.innerWidth < 768 ? 1 : 0.6,
        });
      };

      window.addEventListener("resize", handleResize);
      return () => window.removeEventListener("resize", handleResize);
    }
  }, [wordFrequencies]);

  return (
    <div
      id="wordcloud"
      style={{
        width: "100%",
        height: "400px",
        marginBottom: "20px",
        border: "1px solid #ddd",
        clipPath: "ellipse(60% 49% at 50% 50%)",
        overflow: "hidden",
      }}
    ></div>
  );
};

export default WordCloudComponent;
